import * as React from 'react';
import { Inline } from '@ali/4ever-cangjie';

import { MentionSpan, MentionWrapper } from './styled';
import { getHighlightColor } from '../utils/utils/getHighlightColor';

interface IMentionProps {
  node: Inline;
  style?: React.CSSProperties;
}

const Mention: React.FC<IMentionProps> = (props) => {
  const { children, node, style = {}, ...rest } = props;

  const { searchMatch } = node.data;
  if (searchMatch) {
    const searchSelected = searchMatch.selected;
    style.backgroundColor = getHighlightColor(searchSelected);
  }

  return (
    <MentionWrapper>
      <MentionSpan {...rest} style={style}>{children}</MentionSpan>
    </MentionWrapper>
  );
};

export default Mention;
